@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flex-space-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin roboto-400 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@mixin roboto-500 {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

@mixin roboto-700 {
  font-family: Roboto, sans-serif;
  font-weight: 700;
}
